import "./App.css";
import React from "react";
import "bootstrap/dist/css/bootstrap.css";
import PageSearchResults from "./page-search-results";
import PageHome from "./page-home";
import PagePic from "./page-pic";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import Layout from "./Components/layout";

class App extends React.Component {
  render() {
    return (
      <>
        <BrowserRouter>
          <Layout>
            <Switch>
              <Route exact path="/search" component={PageSearchResults}></Route>
              <Route exact path="/result" component={PagePic}></Route>
              <Route exact path="/find-pictures/" component={PageHome}></Route>
            </Switch>
          </Layout>
        </BrowserRouter>
      </>
    );
  }
}

export default App;
