import React from "react";

class PicInfo extends React.Component {
  state = {
    data: {
      urls: {},
      user: {},
      location: {},
    },
  };

  componentDidMount() {
    this.fetchData();
  }

  async fetchData() {
    this.setState({
      loading: true,
    });
  }

  render() {
    return (
      <>
        <div className="row centrar top50">
          <div className="col-md-12">
            <h5>About this picture</h5>
            <hr />
          </div>
        </div>
        <div className="row mb-5">
          <div className="col-md-4 centrar">
            <strong>User: </strong> {this.props.user}
          </div>
          <div className="col-md-4">
            <strong>Unsplash username: </strong> {this.props.username}
          </div>
          <div className="col-md-4">
            <strong>Location: </strong> {this.props.location}
          </div>
        </div>
      </>
    );
  }
}

export default PicInfo;
