import React from "react";
import "./pic-card.css";
import { Link } from "react-router-dom";

class PicCard extends React.Component {
  render() {
    return (
      <div className="col-3">
        <Link to={"/result?" + this.props.id}>
          <div className="item">
            <img className="pic" src={this.props.img} id={this.props.id} />
          </div>
        </Link>
      </div>
    );
  }
}

export default PicCard;
