import "./App.css";
import React from "react";
import "bootstrap/dist/css/bootstrap.css";
import "./page-pic.css";
import PicInfo from "./Components/pic-info";
import Loading from "./Components/loading";
import Nav from "./Components/nav";

class PagePic extends React.Component {
  state = {
    data: {
      urls: {},
      user: {},
      location: {},
    },
    busqueda: "",
  };

  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  componentDidMount() {
    this.fetchData();
  }

  async fetchData() {
    let pic = this.props.history.location.search.substr(1);
    let url = `https://api.unsplash.com/photos/${pic}/?client_id=n99jfbyp1i4UEweQqOFQQUGsXKAcrYLPc5ZArJa2nxY`;
    this.setState({
      loading: true,
    });
    const response = await fetch(url);
    const data = await response.json();
    console.log(data);

    if (data.errors) {
      this.setState({
        data: data,
        error: true,
        loading: false,
        errorMensaje: data.errors,
      });
    } else {
      this.setState({
        data: data,
        loading: false,
      });
    }
  }

  render() {
    return (
      <>
        <Nav />
        {this.state.loading && <Loading />}
        {this.state.error && <h1>{this.state.errorMensaje}</h1>}
        <div className="container">
          <div className="row centrar">
            <div className="col-md-12">
              <img
                src={this.state.data.urls.regular}
                alt={this.state.data.alt_description}
                className="pic-artist margenes50"
              />
              <p className="description">{this.state.data.description}</p>
            </div>
          </div>
          <PicInfo
            username={this.state.data.user.username}
            user={this.state.data.user.name}
            location={this.state.data.location.name}
          />
        </div>
      </>
    );
  }
}

export default PagePic;
