import React from "react";
import PicCard from "./pic-card";
import Loading from "./loading";

class SearchResults extends React.Component {
  state = {
    loading: false,
    error: null,
    data: {
      results: [],
    },
  };

  componentWillReceiveProps(e) {
    let termino = e.busqueda;
    console.log(termino, "llega");
    this.fetchData(
      `https://api.unsplash.com/search/photos/?query=${termino}&client_id=n99jfbyp1i4UEweQqOFQQUGsXKAcrYLPc5ZArJa2nxY&page=1&per_page=12`
    );
  }

  async fetchData(url) {
    this.setState({
      loading: true,
    });
    const response = await fetch(url);
    const data = await response.json();
    console.log(data);

    if (data.errors) {
      this.setState({
        data: data,
        error: true,
        loading: false,
        errorMensaje: data.errors,
      });
    } else {
      this.setState({
        data: data,
        loading: false,
      });
    }
  }

  render() {
    return (
      <>
        {this.state.loading && <Loading />}
        {this.state.error && <h1>{this.state.errorMensaje}</h1>}
        <div className="container">
          <div className="row gallery">
            {this.state.data.results.map((pic, i) => {
              return <PicCard img={pic.urls.small} id={pic.id} key={i} />;
            })}
          </div>
        </div>
      </>
    );
  }
}

export default SearchResults;
